.card-frame {
    z-index: 1;
    position: relative;
    height: 108%;
    max-width: 97%;
    left: 1%;
    top: 0.5%;
    left: 1.2%;
    display: flex;
    flex-direction: column;
}



.frame-header,
.frame-type-line {
    background: 
        -webkit-linear-gradient( bottom, rgba(201, 216, 201, .3), rgba(201, 216, 209, .3) ), 
        url(/static/media/tile_bg_2.ab80e342.jpg);
