.back {
    overflow: hidden;
    z-index: -1;
    -webkit-transform: rotateY(180deg);
    transform: rotateY(180deg);
    background: url(,,/../../img/cardBack.jpg);
    border: 1px solid black;
    width: 500px;
    height: 700px;
    border-radius: 25px;
    box-sizing: border-box;
    box-shadow: -8px 9px 16px -3px grey;
    background: #171314;
    margin: 0px;
    -webkit-transition: 1s;
    transition: 1s;
    display: inline-block;
    background: url(/static/media/cardBack.e25f28b3.jpg);
    position: absolute;
    top: 0px;
    border-radius: 20px;
}
.back:before {
    content: "";
/*     position: absolute; */
    z-index: -1;
    -webkit-transform: rotateY(180deg);
    transform: rotateY(180deg);
}
.front, .back {
    -webkit-transition: all 0.5s linear;
    transition: all 0.5s linear;
}

.flipped, .hovercard:hover {
    -webkit-transform: rotateY(180deg);
    transform: rotateY(180deg);
}
.flipped .front, .hovercard:hover .front {
    box-shadow: 0 0 0 #aaa;
}
.flipped .back, .hovercard:hover .back {
    box-shadow: 5px 5px 5px #aaa;
}


/* Css Reset */

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
margin: 0;
padding: 0;
border: 0;
font-size: 100%;
font: inherit;
vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
display: block;
}
body {
line-height: 1;
}
ol, ul {
list-style: none;
}
blockquote, q {
quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
content: '';
content: none;
}
table {
border-collapse: collapse;
border-spacing: 0;
}


